@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700,800,900&display=swap');
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans:400,700,800,900&display=swap');
@font-face {
  font-family: 'Codec';
  font-style: normal;
  font-weight: 400;
  src: local('Codec'), local('Codec-Regular'), url(../fonts/codec/Codec-Cold-Regular-trial.ttf) format('truetype');
}
@font-face {
  font-family: 'Aller';
  font-style: normal;
  font-weight: 400;
  src: local('Aller'), local('Aller-Regular'), url(../fonts/Aller/Aller_Rg.ttf) format('truetype');
}
* {
  box-sizing: border-box;
  outline: none;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Sans', sans-serif;
}
img {
  width: 100%;
  height: 100%;
}
a {
  cursor: url(http://aradesign.in/content/common/red-cursor.png) 15 13, default;
}
body {
  cursor: url(http://aradesign.in/content/common/white-cursor.png) 15 13, default;
  padding: 0;
  margin: 0;
  background: #000;
}
body .main-container {
  height: 100vh;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
  z-index: 3;
}
@media only screen and (max-width: 768px) {
  body .main-container {
    padding: 0 25px;
  }
}
@media only screen and (max-width: 558px) {
  body .main-container {
    padding: 0 15px;
  }
}
.main-container {
  height: 100vh;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
}
@media only screen and (max-width: 768px) {
  .main-container {
    padding: 0 25px;
  }
}
.main-container .super-container .row .container.contact-container img {
  cursor: default !important;
}
.main-container .super-container .row .container.contact-container .__react_component_tooltip.place-top {
  z-index: 99;
  padding: 10px;
  background-color: #fff !important;
}
.main-container .super-container .row .container.contact-container .project-lst {
  background: #fff;
}
.main-container .super-container .row .container.contact-container .project-lst .list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}
.main-container .super-container .row .container.contact-container .project-lst .list .image {
  width: 100px;
  margin-right: 10px;
}
.main-container .super-container .row .container.contact-container .project-lst .list .name span {
  font-size: 22px;
  color: #333;
  font-weight: 700;
  line-height: 26px;
  padding: 20px 0 0;
  margin: 0;
  font-family: 'IBM Plex Sans', sans-serif;
}
.main-container .super-container .row .container.contact-container .left-content {
  position: absolute;
  bottom: 0;
  margin-bottom: 25px;
  left: 0;
  padding: 0 15px;
  z-index: 99;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.contact-container .left-content {
    max-width: 100%;
    width: 100vw;
    padding: 20px;
    position: fixed;
    margin-bottom: 0px;
  }
}
.main-container .super-container .row .container.contact-container .left-content .title-text {
  display: flex;
  padding-bottom: 10px;
}
.main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence {
  font-size: 42px;
  color: #fff;
  font-weight: 800;
  line-height: 52px;
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence {
    font-size: 38px;
    line-height: 37px;
  }
}
@media only screen and (max-width: 375px) {
  .main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence {
    font-size: 32px;
    line-height: 37px;
  }
}
.main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence span {
  font-size: 38px;
  color: #fff;
  font-weight: 800;
  line-height: 52px;
  font-family: 'Montserrat', sans-serif;
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence span {
    font-size: 38px;
    line-height: 37px;
  }
}
@media only screen and (max-width: 375px) {
  .main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence span {
    font-size: 32px;
    line-height: 37px;
  }
}
@media screen and (max-width: 768px) {
  .main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence {
    font-size: 24px;
    line-height: 32px;
  }
}
@media screen and (max-width: 320px) {
  .main-container .super-container .row .container.contact-container .left-content .title-text .rw-sentence {
    font-size: 18px;
    line-height: 28px;
  }
}
.main-container .super-container .row .container.contact-container .left-content p {
  font-size: 18px;
  color: #fff;
  font-weight: 400;
  line-height: 28px;
  margin: 0;
  width: 665px;
  padding: 20px 0 0;
  font-family: 'Montserrat', sans-serif;
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.contact-container .left-content p {
    font-size: 14px;
    line-height: 18px;
    padding: 20px 0;
    width: 100%;
  }
}
.main-container .super-container .row .container.contact-container .left-content h4 {
  font-size: 18px;
  color: #fff;
  font-weight: 400;
  line-height: 28px;
  padding: 20px 0 0;
  margin: 0;
  font-family: 'IBM Plex Sans', sans-serif;
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.contact-container .left-content h4 {
    font-size: 16px;
    line-height: 23px;
  }
}
.main-container .super-container .row .container.contact-container .left-content h4 a {
  font-size: 18px;
  color: #fff;
  font-weight: 400;
  line-height: 28px;
  padding: 20px 0 0;
  margin: 0;
  font-family: 'IBM Plex Sans', sans-serif;
  text-decoration: none;
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.contact-container .left-content h4 a {
    font-size: 16px;
    line-height: 23px;
  }
}
